<template>
  <div class="bib">
    <div v-for="(d,i) in data" v-bind:key="d.key">
      <div v-if="i==0 || d.year != data[i-1].year" class="year">
        {{d.year}}
      </div>
      <div>
        <div class="dt">
          [{{i+1}}]
        </div>
        <div class="dd">
          <div v-html="d.html"></div>
          <div>
          [
          <b-button v-b-toggle="`card-${bkey}-${i}`" variant="link">bib</b-button>
          <span v-for='(v,k) in d.links' v-bind:key="k">
            |<b-button variant="link" :href="v">{{k}}</b-button>
          </span>]
          </div>
          <b-collapse :id="`card-${bkey}-${i}`">
            <b-card  border-variant="primary">
              <b-button v-clipboard:copy="d.bib" size="sm"
                        variant="info">copy</b-button>
              <b-button size="sm" v-b-toggle="`card-${bkey}-${i}`" variant="info">close</b-button>
              <pre>{{d.bib}}</pre>
            </b-card>
          </b-collapse>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Bib',
    props: {
      bkey: String,
      data: Array
    }
  }
</script>

<style scoped>
  .btn-link {
      color: #BD5D38;
  }
  .year {
      font-size: 150%;
      width: 35%;
      margin-top: 10px;
      margin-bottom: 5px;
      font-weight: bold;
  }
  .dt {
      float: left;
      clear: left;
      display: inline;
      width: 4%;
      font-weight: bold;
  }
  .dd {
      display: inline-block;
      margin-left: 5px;
      width: 95%;
  }
</style>



