<template>
  <div id="app">
    <nav class="navbar navbar-expand-lg navbar-dark bg-primary fixed-top" id="sideNav">
      <a class="navbar-brand js-scroll-trigger" href="#page-top">
        <span class="d-block d-lg-none">Shenghuo ZHU</span>
        <span class="d-none d-lg-block">
          <img class="img-fluid img-profile rounded-circle mx-auto mb-2"
               src="/static/profile.jpg" alt="">
        </span>
      </a>

      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent"
              aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav">
          <li class="nav-item">
            <a class="nav-link js-scroll-trigger" href="#about">About</a>
          </li>
          <li class="nav-item">
            <a class="nav-link js-scroll-trigger"
               href="#patents">Patents</a>
          </li>
          <li class="nav-item">
            <a class="nav-link js-scroll-trigger"
               href="#publication">Publication</a>
          </li>
          <li class="nav-item">
            <a class="nav-link js-scroll-trigger"
               href="#tr">Tech Reports</a>
          </li>
        </ul>
      </div>
    </nav>

    <div class="container-fluid p-0">
      <section class="resume-section p-3 p-lg-5 d-flex d-column" id="about">
        <div class="my-auto" >
          <about/>
        </div>
      </section>

      <hr class="m-0"/>

      <section class="resume-section p-3 p-lg-5 d-flex flex-column" id="patents">
        <div class="my-auto" >
          <h2 class="mb-5">Patents</h2>
        </div>
        <bib bkey="patent" :data="dataPatent"/>
      </section>

      <hr class="m-0"/>

      <section class="resume-section p-3 p-lg-5 d-flex flex-column"
               id="publication">
        <div class="my-auto">
          <h2 class="mb-5">Publication</h2>
        </div>
        <bib bkey="pub" :data="dataPub"/>
      </section>

      <hr class="m-0"/>

      <section class="resume-section p-3 p-lg-5 d-flex flex-column"
               id="tr">
        <div class="my-auto">
          <h2 class="mb-5">Tech Reports</h2>
        </div>
        <bib bkey="tr" :data="dataTr"/>
      </section>
    </div>
  </div>
</template>

<script>
  import About from './components/About.vue'
  import Bib from './components/Bib.vue'
  import Patent from './assets/granted.json'
  import Pub from './assets/pub.json'
  import Tr from './assets/tr.json'

  export default {
    name: 'App',
    components: {
      'about': About,
      'bib': Bib
    },
    data () {
      return {
        title: 'Shenghuo Zhu',
        dataTr: Tr,
        dataPub: Pub,
        dataPatent: Patent
      }
    },
    mounted () {
      const $ = this.jquery
      this.$nextTick(() => {
        $('a.js-scroll-trigger[href*="#"]:not([href="#"])').click(function () {
          if (location.pathname.replace(/^\//, '') === this.pathname.replace(/^\//, '') && location.hostname === this.hostname) {
            var target = $(this.hash)
            target = target.length ? target : $('[name=' + this.hash.slice(1) + ']')
            if (target.length) {
              $('html, body').animate({
                scrollTop: (target.offset().top)
              }, 1000, 'easeInOutExpo')
              return false
            }
          }
        })
        // Closes responsive menu when a scroll trigger link is clicked
        $('.js-scroll-trigger').click(function () {
          $('.navbar-collapse').collapse('hide')
        })
        // Activate scrollspy to add active class to navbar items on scroll
        $('body').scrollspy({
          target: '#sideNav'
        })
      })
    },
    methods: {
          track () {
            this.$ga.page('/')
          }
    }
  }
</script>

<style>
  #app {
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
  }
</style>



