<template>
<div class="email">
  <a :href='"mailto:"+vid+"@"+vdomain'>{{vid}}@{{vdomain}}</a>
</div>
</template>

<script>
  export default {
    name: 'Email',
    props: [ 'uid', 'domain' ],
    data () {
      return {
        vid: '',
        vdomain: ''
      }
    },
    async mounted () {
      this.vid = this.uid
      this.vdomain = this.domain
    }
  }
</script>



